import React, { useState, useEffect, Fragment, } from "react";
import { useDispatch, useSelector } from "react-redux";
import Header from '../components/Header';
import Leftnav from '../components/Leftnav';
import Rightchat from '../components/Rightchat';
import Loader from "react-loader-spinner";
import axios from 'axios';
import api from '../utils/api';
const Password=()=> {
    const dispatch=useDispatch();
    const {
        user
    } = useSelector(state => state.auth); // here, indicate reducer

    const [video,setvideo]=useState(false);
    const [desc, setdesc]=useState('');
    const [saleoption, setSaleoption]=useState(0);
    const [fileUrl, updateFileUrl] = useState(null);
    const [uploading, setUploading]=useState(false);

    const keydown = (e) =>{
        return e.keyCode;
    }
    const eventdesc=(e)=>{
        let length=e.target.value.length;
        if(length<=349)
            setdesc(e.target.value);
        if(keydown===8||keydown===46)
            setdesc(e.target.value);
    }
    const onChangeSale=(e)=>{
        e.preventDefault();
        setSaleoption(e.target.value);
    }
    const Uploadsubmit=async(e)=>{
        e.preventDefault();
        console.log(saleoption);
        setUploading(true);
        if(desc!=="" && fileUrl){
            let formData = new FormData();
            formData.append('file', fileUrl);
            formData.append('desc',desc);
            formData.append('saleoption',saleoption);
            axios.post('https://viavix.com/api/blog',
            formData,
            {
                headers: {
                    'x-auth-token':  localStorage.getItem('token'),
                    'Content-Type': 'multipart/form-data'
                }
            }
            ).then(function(res){
                if(res.data==="invalid")
                    window.alert("This file is invalid so that you can't upload.");
                else if(res.data==="failure")
                    window.alert("Uploading is failed. Please try again.");
                else if(res.data==="existing")
                    window.alert("This file already exists so that you can't upload.");
                else
                {    
                    window.alert("success upload");
                }
                setUploading(false);
                updateFileUrl(null);
            })
            .catch(function(){
                updateFileUrl(null);
                setUploading(false);
            });
        }
    }
    const capturefile=async(e)=> {
        e.preventDefault();
        updateFileUrl(null);
        try{ 
            const file = e.target.files[0];
            updateFileUrl(file);
            if(!file) return;
        } catch(e){
            window.alert(e);
        }
    }
    return (
        <Fragment>
            <div className="main-wrapper">
                <Header />
                <Leftnav />
                {/* <Rightchat /> */}
                <div className="main-content bg-lightblue theme-dark-bg right-chat-active">
                    <div className="middle-sidebar-bottom">
                        <div className="middle-sidebar-left">
                            <div className="middle-wrap">
                                <div className="card w-100 border-0 bg-white shadow-xs p-0 mb-4">
                                    <div className="flex-justify-content w-100 bg-black border-0 d-flex rounded-3">
                                       <div className="d-flex w-100 mt-2 p-4 upload" >
                                        <h4 className="font-md text-white fw-600 ms-4 mb-0 mt-2 ">Upload Videos and Photos</h4></div>
                                    </div> 
                                    <div className="card-body p-lg-5 p-4 w-100 border-0">
                                        <form action="#">
                                            <div className="row">
                                                <div className="col-lg-12 mb-1">
                                                    <div className="form-gorup">
                                                        <label className="text-gray mont-font fw-600 font-md">Description</label>
                                                        <textarea value={desc} onChange={eventdesc} onKeyDown={keydown} name="nft-symbol" className="form-control fw-500 font-sm" placeholder="Type less than 350 characters" required={true}/>
                                                    </div>
                                                </div>
                                            </div>

                                            <div className="row">
                                                <div className="col-lg-12 mb-1">
                                                    <div className="form-gorup">
                                                        <label className="text-gray mont-font fw-600 font-md">Resource <br/></label>
                                                        <input type="file" name="file" onChange={capturefile} id="file" className="input-file" required={true}/>
                                                        
                                                            <label htmlFor="file" className="rounded-3 text-center bg-white btn-tertiary js-labelFile p-4 w-100 border-dashed">
                                                                <span className="js-fileName">Drag and drop or click to upload</span>
                                                            </label>
                                                        
                                                        {/* {uploading?
                                                        <div className="card w-100 text-center border-0 p-1 mt-1">
                                                            <div className="snippet mt-2 ms-auto me-auto" data-title=".dot-typing">
                                                                <div className="stage">
                                                                    <div className="dot-typing"></div>
                                                                </div>
                                                            </div>
                                                        </div>
                                                        :''} */}
                                                        <div className="d-flex justify-content-center m-3">  
                                                            {!fileUrl?'':
                                                            (fileUrl.type.indexOf("image")===0?
                                                                <img height={fileUrl?"200":"0" } width="*" src={fileUrl? URL.createObjectURL(fileUrl) : null} alt={fileUrl? fileUrl.name : null}/>:
                                                                <video height={fileUrl?"400":"0" } width="*" controls>
                                                                    <source src={URL.createObjectURL(fileUrl)}/>
                                                                </video>
                                                            )
                                                            }
                                                        </div>    
                                                    </div>                                                                                     
                                                </div>
                                            </div>
                                            <div className="row" value='saleoption' onChange={onChangeSale}>                                    
                                                <label className="text-yellow">Choose payment to download?</label> 
                                                <div className="col-lg-3 mb-3">
                                                    <input type="radio" id="freeradio" name="pay_way" value={0} defaultChecked></input>
                                                    <label htmlFor="freeradio" className="pl-2 text-success mont-font fw-600 font-sm">Free</label>
                                                </div>    
                                                <div className="col-lg-3 mb-3">
                                                    <input type="radio" id="viacoinradio" className="fw-600 font-lg" name="pay_way" value={1} ></input>
                                                    <label htmlFor="viacoinradio" className="pl-2 text-success mont-font fw-600 font-sm">Viacoin</label>
                                                </div>   
                                            </div>
                                            <div className="row">
                                                <div className="d-flex col-lg-12 mb-0 justify-content-center">
                                                        
                                                {uploading?
                                                    <Loader
                                                    type="Oval"
                                                    color="#000"
                                                    height={80}
                                                    width={80}
                                                    />
                                            
                                                :
                                                    <button onClick={Uploadsubmit} className="bg-black text-center text-white font-md fw-600 p-3 w175 badge-pill d-inline-block">Post</button>
                                                }
                                                </div>
                                            </div>

                                        </form>
                                    </div>
                                </div>
                            </div>
                        </div>

                    </div>
                </div>
            </div>

        </Fragment>
    );
}

export default Password;