import {
  USER_LOADED,
  SET_VCOIN,
  USER_AUTHENTICATED
} from "../types";

const initialState = {
  user:null,
  vcoin:0,
  isauthenticated:null
};

export default (state = initialState, action) => {
  switch (action.type) {
    case USER_LOADED:
      return {
        ...state,
        user: action.payload,
      }
    case SET_VCOIN:
      return {
        ...state,
        vcoin: action.payload,
      }
    case USER_AUTHENTICATED:
      return {
        ...state,
        isauthenticated: action.payload,
      }
    default: 
      return state
  }
};
