import React, { useState, useEffect, Fragment } from "react";
import { useDispatch, useSelector } from "react-redux";
import { useHistory, Link } from "react-router-dom";
import { Watermark } from "@hirohe/react-watermark";
import api from '../utils/api';
const Postview = ({
  creator,
  time,
  des,
  avater,
  sourceURI,
  videoOption,
  id,
  price,
  likes,
  buyVcoin,
  Download,
  addlike
}) => {
  const [isOpen, setIsOpen] = useState(false);
  const [isActive, setIsActive] = useState(false);
  const [loading, setLoading] = useState(true);
  const [owneraddr, setowneraddr] = useState(true);
  const [username, setUsername] = useState("");

  const toggleOpen = () => setIsOpen({ isOpen: !isOpen });
  const toggleActive = () => setIsActive({ isActive: !isActive });

  const menuClass = `${isOpen ? " show" : ""}`;
  const emojiClass = `${isActive ? " active" : ""}`;
  const { user, isauthenticated } = useSelector((state) => state.auth); // here, indicate reducer
  useEffect(() => {
    const showusername = async () => {
      const res= await api.post('/auth/name',{id:creator});
      setUsername(res.data);
    };
    if(isauthenticated) showusername();
  }, [isauthenticated]);
  return (
    <div className="card w-100 shadow-xss rounded-xxl border-0 p-4 mb-3">
      <div className="card-body p-0 d-flex">
        <figure className="avatar me-3">
          <img
            src={`assets/images/${avater}`}
            alt="avater"
            className="shadow-sm rounded-circle w45"
          />
        </figure>
        <h4 className="fw-700 text-grey-900 font-xssss mt-1">
          <Link className="mentiontag" to="">
            {" "}
            @{username}
          </Link>{" "}
          <span className="d-block font-xssss fw-500 mt-1 lh-3 text-grey-500">
            {" "}
            {time}
          </span>
        </h4>
        <div className="ms-auto pointer text-white">
          {/* <i className="ti-more-alt text-grey-900 btn-round-md bg-greylight font-xss"></i> */}
        </div>
      </div>
      {videoOption ==="video" ? (
        <div className="card-body p-0 mb-3 rounded-3 overflow-hidden uttam-die">
            <video autoPlay loop className="float-right w-100">
              <source src={sourceURI} type="video/mp4" />
            </video>
        </div>
      ) : null}
      {videoOption === "image" ? (
        <div className="card-body d-block p-0 mb-3">
          <div className="row ps-2 pe-2">
            <div className="col-sm-12 p-1">
              <Watermark text="viavix">
                <img src={sourceURI} className="rounded-3 w-100" alt="post" />
              </Watermark>
            </div>
          </div>
        </div>
      ) : null}
      <div className="card-body p-0 me-lg-5">
        <p className="fw-500 text-grey-500 lh-26 font-xssss w-100 mb-2">
          {des}
          <a href="/home" className="fw-600 text-primary ms-2">
            See more
          </a>
        </p>
      </div>
      <div className="card-body d-flex p-0 justify-content">
        <div
          className="card-body p-0 emoji-bttn pointer d-flex align-items-center fw-600 text-grey-900 text-dark lh-26 font-xssss me-2"
          onClick={()=>addlike(id)}
        >
          <i className="feather-heart text-red me-2 btn-round-xs font-lg"></i>
          <span className="d-none-xss text-red">{likes}</span>
        </div>
        <div
          className={`card-body p-0 pointer ms-auto d-flex align-items-center fw-600 text-grey-900 text-dark lh-26 font-xssss ${menuClass}`}
          id={`dropdownMenu${id}`}
          data-bs-toggle="dropdown"
          aria-expanded="false"
          onClick={toggleOpen}
        >
          <i className="feather-share-2 text-grey-900 text-dark btn-round-sm font-md"></i>
          <span className="d-none-xs">Share</span>
        </div>
        <div className="card-body p-0 d-flex">
          <a
            href="/defaultvideo"
            className="d-flex align-items-center fw-600 text-grey-900 text-dark lh-26 font-xssss"
          >
            <i className="feather-alert-circle text-dark text-grey-900 btn-round-sm font-lg"></i>
            <span className="d-none-xss">Report</span>
          </a>
        </div>

        <div className="card-body p-0 d-flex">
          {price !== 0 ?
            <button
              className="border-0 bg-transparent d-flex align-items-center fw-600 text-grey-900 lh-26 font-xssss text-dark"
              onClick={() => buyVcoin(id, price)}
            >
              <i className="feather-download btn-round-sm font-xs text-dark"></i>
              Buy
            </button>:         
            <button
              className="border-0 bg-transparent d-flex align-items-center fw-600 text-grey-900 lh-26 font-xssss text-dark"
              onClick={() => Download(id)}
            >
              <i className="feather-download btn-round-sm font-xs text-dark"></i>
              Download
            </button>
            }
        </div>
        {/* <div className="card-body p-0 d-flex">
                { loading && price!==0?
                    <button className="border-0 bg-transparent d-flex align-items-center fw-600 text-grey-900 lh-26 font-xssss text-dark" onClick={()=>buyNft(id,price)}><i className="feather-award btn-round-sm font-xs text-dark"></i>NFT</button>
                    :<button className="border-0 bg-transparent d-flex align-items-center fw-600 text-grey-900 lh-26 font-xssss" disabled><i className="feather-award btn-round-sm font-xs"></i>NFT</button>
                }   
                </div> */}
        {/* <div className={`card-body p-0 d-flex emoji-wrap pointer ${emojiClass}`}>
                    <ul className="emojis list-inline mb-0">
                        <li className="emoji list-inline-item"><i className="em em---1"></i> </li>
                        <li className="emoji list-inline-item"><i className="em em-angry"></i></li>
                        <li className="emoji list-inline-item"><i className="em em-anguished"></i> </li>
                        <li className="emoji list-inline-item"><i className="em em-astonished"></i> </li>
                        <li className="emoji list-inline-item"><i className="em em-blush"></i></li>
                        <li className="emoji list-inline-item"><i className="em em-clap"></i></li>
                        <li className="emoji list-inline-item"><i className="em em-cry"></i></li>
                        <li className="emoji list-inline-item"><i className="em em-full_moon_with_face"></i></li>
                    </ul>
                </div> */}

        {/* <div className={`card-body p-0 d-flex dropdown-menu dropdown-menu-end p-4 rounded-xxl border-0 shadow-lg right-0 ${menuClass}`} aria-labelledby={`dropdownMenu${id}`}>
                    <h4 className="fw-700 font-xss text-grey-900 d-flex align-items-center">Share <i className="feather-x ms-auto font-xssss btn-round-xs bg-greylight text-grey-900 me-2"></i></h4>
                    <div className="card-body p-0 d-flex">
                        <ul className="d-flex align-items-center justify-content-between mt-2">
                            <li className="me-1"><span className="btn-round-lg pointer bg-facebook"><i className="font-xs ti-facebook text-white"></i></span></li>
                            <li className="me-1"><span className="btn-round-lg pointer bg-twiiter"><i className="font-xs ti-twitter-alt text-white"></i></span></li>
                            <li className="me-1"><span className="btn-round-lg pointer bg-linkedin"><i className="font-xs ti-linkedin text-white"></i></span></li>
                            <li className="me-1"><span className="btn-round-lg pointer bg-instagram"><i className="font-xs ti-instagram text-white"></i></span></li>
                            <li><span className="btn-round-lg pointer bg-pinterest"><i className="font-xs ti-pinterest text-white"></i></span></li>
                        </ul>
                    </div>
                    <div className="card-body p-0 d-flex">
                        <ul className="d-flex align-items-center justify-content-between mt-2">
                            <li className="me-1"><span className="btn-round-lg pointer bg-tumblr"><i className="font-xs ti-tumblr text-white"></i></span></li>
                            <li className="me-1"><span className="btn-round-lg pointer bg-youtube"><i className="font-xs ti-youtube text-white"></i></span></li>
                            <li className="me-1"><span className="btn-round-lg pointer bg-flicker"><i className="font-xs ti-flickr text-white"></i></span></li>
                            <li className="me-1"><span className="btn-round-lg pointer bg-black"><i className="font-xs ti-vimeo-alt text-white"></i></span></li>
                            <li><span className="btn-round-lg pointer bg-whatsup"><i className="font-xs feather-phone text-white"></i></span></li>
                        </ul>
                    </div>
                    <h4 className="fw-700 font-xssss mt-4 text-grey-500 d-flex align-items-center mb-3">Copy Link</h4>
                    <i className="feather-copy position-absolute right-35 mt-3 font-xs text-grey-500"></i>
                    <input type="text" placeholder="https://socia.be/1rGxjoJKVF0" className="bg-grey text-grey-500 font-xssss border-0 lh-32 p-2 font-xssss fw-600 rounded-3 w-100 theme-dark-bg" />
                </div> */}
      </div>
    </div>
  );
};

export default Postview;
